*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

header{
    color: white;
    background-color:black;
    padding: 3vh 4vw;
    position: fixed;
    z-index: 99;
    width: 100%;
    top: 0;
}

.header--logo{
    width: 100px;
    border-radius:50%;
}

.header--info{
    display: flex;
    flex-direction: row;
    align-items:center;
    justify-content: space-between;
}

.header--menu ul{
    display: flex;
    flex-direction: row;
    align-items:center;
    justify-content: space-between;
    list-style: none;
}

.header--menu ul li{
    margin-left: 20px;
}

.header--menu ul li a{
    text-decoration: none;
    color: white;
    font-size: 23px;
    font-family:'Mukta', sans-serif ;
    transition: all ease 0.2s;
}

.header--menu ul li a:hover{
    color: orange;
    border-bottom: 2px orange solid;
}

.span{
    border-bottom: 2px white solid;
}

.tela{
    height: auto;
    margin: 30vh 30px;
    background-color: black;
    opacity: 90%;
    
}

footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color:black;
    height: auto;
}

.footer--logo{
    margin-top: 10vh;
    border-radius: 50%;
    width: 100px;
}

.footer--description{
    color: white;
    text-indent: 30px;
    text-align: justify;
    margin: 10vh 5vw;
    font-family: 'Mukta', sans-serif;
    font-size: 20px;
}

.footer--title{
    color: white;
    font-family: 'Mukta', sans-serif;
    border-bottom: 2px white solid;
    padding: 0px 10vw;
}

.footer--nomeDev{
    color: white;
    margin: 5vh 0px;
    font-family: 'Mukta', sans-serif;
    font-size: 20px;
}
.produtos--title{
    font-size:40px;
    color: white;
    padding: 7vh 4vw;
    font-family: 'Mukta', sans-serif;
}

p strong{
    color: orange;
}

.produtos--text{
    color: white;
    margin: 3vh 4vw;
    font-size: 25px;
    font-family: 'Mukta', sans-serif;
}

.produtos--buttons{
    display: flex;
    justify-content:space-evenly;
}

.produtos--buttons a{
    background-color:orange;
    text-decoration: none;
    margin: 10vh 0px;
    padding: 2vh 4vw;
    font-family: 'Mukta', sans-serif;
    font-size:20px;
    border-radius: 15px;
    color: black;
    transition: all ease 0.3s;
}

.produtos--buttons a:hover{
    background-color:white;
    color: orange;
}
.tela h2{
    color: white;
    text-align: center;
    padding-top: 10px;
    border-bottom: 2px white solid;
    margin: 0px 40px;
    font-size: 20px;
    font-family: 'Mukta', sans-serif;
}

.tela--devName{
    color: white;
    text-decoration: none;
    margin: 20px 40px;
}

.tela--devName a{
    -webkit-text-decoration: 2px underline;
            text-decoration: 2px underline;
    color: white;
    cursor: pointer;
    transition: all ease 0.3s;
}

.tela--devName a:hover{
    color: orange;
    text-decoration: none;
}


.forms{
    margin: 30vh auto;
    max-width: 500px;
    background-color:white;
    height: auto;
    padding: 30px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    box-shadow: 0px 0px 4px 1px;
}


.forms h3{
    color: orange;
    font-size: 25px;
    text-align: center;
    margin-bottom: 3vh;
}

.forms--item{
    color: gray;
    font-size: 16px;
    margin-bottom: 2vh;
    text-transform: uppercase;
    font-weight: bold;
}

.forms--item input{
    width: 100%;
    padding:10px;
    border-radius: 5px;
    border: 1px solid gray;
}

.forms--item select{
    padding:7px 10px;
    border-radius: 5px;
}

.forms--item textarea{
    width:100%;
    min-height: 25vh;
    border-radius: 5px;
    padding:10px;
    font-family:'Mukta', sans-serif;
}

.forms--button input{
    width: 100%;
    background-color: orange;
    color: white;
    padding: 5px;
    border: none;
    border-radius: 5px;
    font-family:'Mukta', sans-serif;
    cursor: pointer;
    text-transform: uppercase;
    transition: all ease 0.3s;
}

.forms--button input:hover{
    background-color: gray;
    color: orange;
}

